
$mobile: 1160px;

@mixin mobile {
  @media screen and (max-width: #{$mobile}) {
    @content
  }
}

@mixin desktop {
  @media screen and (min-width: #{$mobile}) {
    @content
  }
}


.news_wrapper {
  .desktop {
    @include mobile {
      display: none;
    }
  }
  .mobile {
    @include desktop {
      display: none;
    }
  }
  padding-bottom: 30px;
  h1 {
    font-size: 50px;
    font-weight: 700;
    padding-bottom: 15px;
    @include mobile {
      padding-bottom: 10px;
      margin-bottom: 10px;
      margin-top: 0px;
      font-size: 32px;
      font-weight: 600;
    } 
  }
  
  .news_wrapp{
    display: flex;
    justify-content: space-between;
    @include mobile {
      display: block;
    }
    .pic{
      position: relative;
      width: 75%;
      height: 500px;
      object-fit: cover;
      overflow: hidden;
      border-radius: 10px;
      cursor: pointer;
      @include mobile {
        width: 100%;
        height: 350px;
      }
    }
    img {
      border-radius: 10px;
      height: 500px;
      transform: scale(1);
      transition: transform .4s;
   
      cursor: pointer;
      position: relative;
    }
    .pic:hover img{
   
      transform: scale(1.2);
    }
    .news_title {
      position: absolute;
      padding-left: 40px;
      bottom: 40px;
      width: 80%;
      font-size: 30px;
      font-weight: 600;
      color: white;
      @include mobile {
        font-size: 22px;
      }
    }
    .right_news{
      margin-left: 80px;
      padding-bottom: 10px;
      padding-top: 10px;
      border-bottom: 1px solid #e4e4e5;
      width: 85%;
      @include mobile {
        margin-left: auto;
        margin-right: auto;
        width: 100%;
      }
      .righttitle{
        cursor: pointer;
        font-weight: 500;
        font-size: 18px;
      }
    }
    .without_border{
      border-bottom: 0px solid #e4e4e5;
    }
  }
 
}
.lenktonews{
  background-color: #f2f7ff;
  height: 50px;
  border-radius: 10px;
  font-size: 18px;
  width: 200px;
  margin-top: 50px;
  padding-left: 40px;
  padding-right: 35px;
  padding-bottom: 5px;
  margin-left: 690px;
  color: #3662ff;
  @include mobile {
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
  }
}
.lenktonews:hover {
  background-color: #e5ecf8;
}
.linkimgwrapper{
  display: flex;
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 50px;
  .linkimg{
    width: 360px;
    height: 150px;
    
  }
}
